body {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(30, end) infinite hands-rotate;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite hands-rotate;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up > .stopwatch__minutes-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up > .stopwatch__seconds-hand {
  animation-duration: 10s;
}

@keyframes hands-rotate {
  50% {
    transform: rotate(180deg)translateX(50%);
  }

  100% {
    transform: rotate(360deg)translateX(-50%);
  }
}
